import { Pagination, Button, Select, message } from "antd";
import Loader from "../../../common/loader/Loader";
import { useEffect, useState } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getPayoutReport } from "../../../api/login/Login";
import { FaSort } from "react-icons/fa";

/* ================= UTILS ================= */
const safe = (val, fallback = "-") =>
  val === undefined || val === null || val === "" ? fallback : val;

function PayoutReport() {
  /* ================= STATE ================= */
  const [data, setData] = useState([]);
  const [excelData, setExcelData] = useState([]);
  const [loading, setLoading] = useState(false);

  const [page, setPage] = useState(0);
  const [count, setCount] = useState(10);
  const [totalCount, setTotalCount] = useState(0);

  const [filter, setFilter] = useState({
    start_date: "",
    end_date: "",
    status: "",
    receiver_name: "",
    txn_id: "",
  });

  const currentDate = new Date().toISOString().split("T")[0];

  /* ================= API ================= */
  const fetchReport = async (overrideFilter) => {
    try {
      setLoading(true);
      const activeFilter = overrideFilter || filter;

      // Date validation
      if (
        activeFilter.start_date &&
        activeFilter.end_date &&
        new Date(activeFilter.start_date) > new Date(activeFilter.end_date)
      ) {
        message.error("Wrong Date Combination");
        return;
      }

      const params = {
        page: page + 1,
        limit: count,
        start_date: activeFilter.start_date || "",
        end_date: activeFilter.end_date || "",
        status: activeFilter.status === "ALL" ? "" : activeFilter.status || "",
        receiver_name: activeFilter.receiver_name || "",
        txn_id: activeFilter.txn_id || "",
      };

      const res = await getPayoutReport(params);

      setData(res?.data?.data || []);
      setExcelData(res?.data?.data || []);
      setTotalCount(res?.data?.total || 0);
    } catch (error) {
      message.error(error?.response?.data?.message || "Something went wrong!");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchReport();
  }, [page, count]);

  const handleChange = (e) => {
    const { name, value } = e.target;

    setFilter((prev) => {
      if (name === "start_date" && prev.end_date && value > prev.end_date) {
        return {
          ...prev,
          start_date: value,
          end_date: value,
        };
      }

      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const handleSearch = () => {
    setPage(0);
    fetchReport(filter);
  };

  const resetData = () => {
    const empty = {
      start_date: "",
      end_date: "",
      status: "",
      receiver_name: "",
      txn_id: "",
    };
    setFilter(empty);
    setPage(0);
    fetchReport(empty);
  };

  /* ================= UI ================= */
  return (
    <>
      {loading && <Loader />}

      {/* ================= HEADING ================= */}
      <div className="PageHeading">
        <h1 style={{ color: "var(--bs-primary2)", fontWeight: 600 }}>
          Payout Transaction Report
        </h1>
      </div>

      <div
        className="ContentArea"
        style={{
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1px solid var(--bs-primary2)",
          borderRadius: "12px",
          padding: "16px",
        }}
      >
        {/* ================= FILTER ================= */}
        <div className="card">
          <div className="card-body">
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-3">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control input-white-bg"
                  max={currentDate}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control input-white-bg"
                  max={currentDate}
                  value={filter.end_date}
                  onChange={handleChange}
                  min={filter.start_date || undefined}
                  onBlur={() => {
                    if (
                      filter.start_date &&
                      filter.end_date &&
                      filter.end_date < filter.start_date
                    ) {
                      message.error(
                        "End date cannot be earlier than start date",
                      );
                      setFilter((prev) => ({
                        ...prev,
                        end_date: prev.start_date,
                      }));
                    }
                  }}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Status</label>
                <select
                  className="form-select"
                  name="status"
                  value={filter.status}
                  onChange={handleChange}
                >
                  <option value="">Select Status</option>
                  <option value="ALL">All</option>
                  <option value="PROCESSING">Processing</option>
                  <option value="SUCCESS">Success</option>
                  <option value="FAILED">Failed</option>
                </select>
              </div>

              <div className="form-group col-md-3">
                <label>Receiver Name</label>
                <input
                  type="text"
                  name="receiver_name"
                  className="form-control"
                  value={filter.receiver_name}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-12 mt-3">
                <Button type="primary" onClick={handleSearch}>
                  Search
                </Button>
                <Button className="ms-2" onClick={resetData}>
                  Reset
                </Button>
              </div>
            </div>
          </div>
        </div>

        {/* ================= TABLE ================= */}
        <div className="card mt-4">
          <div className="card-body">
            <div className="d-flex justify-content-end mb-3">
              <Button
                onClick={() => document.getElementById("excel-btn").click()}
              >
                Download Excel
              </Button>

              <ReactHTMLTableToExcel
                id="excel-btn"
                className="d-none"
                table="table-to-xlsx"
                filename="Payout_Report"
                sheet="Sheet1"
              />
            </div>

            <div style={{ overflow: "auto" }}>
              <table className="table table-striped table-bordered table-hover">
                <thead>
                  <tr>
                    <th>S.No </th>
                    <th>Date </th>
                    <th>Receiver </th>
                    <th>Bank </th>
                    <th>IFSC </th>
                    <th style={{ whiteSpace: "nowrap" }}>Account / UPI </th>
                    <th style={{ whiteSpace: "nowrap" }}>Txn Mode </th>
                    <th style={{ whiteSpace: "nowrap" }}>Account Type</th>
                    <th>Amount </th>
                    <th>Status </th>
                    <th>UTR </th>
                    <th style={{ whiteSpace: "nowrap" }}>External Txn </th>
                    <th>Remarks </th>
                  </tr>
                </thead>

                <tbody>
                  {data?.length > 0 ? (
                    data.map((item, i) => (
                      <tr key={item?._id || i}>
                        <td>{i + 1 + page * count}</td>

                        <td>
                          {item?.createdAt
                            ? new Date(item.createdAt).toLocaleString()
                            : "-"}
                        </td>

                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.receiver_name)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.bank_name)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.ifsc_code)}
                        </td>

                        <td style={{ whiteSpace: "nowrap" }}>
                          {item?.txn_mode === "UPI"
                            ? safe(item?.upi_id)
                            : safe(item?.account_number)}
                        </td>

                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.txn_mode)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.account_type)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {item?.amount ?? 0}
                        </td>

                        <td style={{ whiteSpace: "nowrap" }}>
                          <span
                            style={{
                              display: "inline-block",
                              padding: "6px 14px",
                              borderRadius: "999px",
                              fontSize: "12px",
                              fontWeight: 600,
                              color: "#fff",
                              textTransform: "capitalize",
                              backgroundColor:
                                item?.status === "SUCCESS"
                                  ? "#16a34a"
                                  : item?.status === "PROCESSING"
                                    ? "#f59e0b"
                                    : item?.status === "FAILED"
                                      ? "#dc2626"
                                      : "#6b7280",
                            }}
                          >
                            {safe(item?.status)}
                          </span>
                        </td>

                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.utr)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.external_txnId)}
                        </td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          {safe(item?.remarks)}
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="13" className="text-center">
                        No Record Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>

            {/* ================= PAGINATION ================= */}
            <div className="d-flex justify-content-between align-items-center mt-3">
              <div className="d-flex align-items-center">
                <label className="me-2">Show</label>
                <Select
                  value={count}
                  onChange={(v) => {
                    setCount(v);
                    setPage(0);
                  }}
                  style={{ width: 120 }}
                  options={[
                    { value: 10, label: "10 / page" },
                    { value: 20, label: "20 / page" },
                    { value: 50, label: "50 / page" },
                    { value: 100, label: "100 / page" },
                  ]}
                />
                <div className="ms-3">Total {totalCount} entries</div>
              </div>

              <Pagination
                pageSize={count}
                current={page + 1}
                total={totalCount}
                onChange={(p) => setPage(p - 1)}
                showSizeChanger={false}
              />
            </div>
          </div>
        </div>
      </div>

      {/* ================= EXCEL TABLE ================= */}
      <table className="d-none" id="table-to-xlsx">
        <thead>
          <tr>
            <th>Date</th>
            <th>Receiver</th>
            <th>Bank</th>
            <th>IFSC</th>
            <th>Account No</th>
            <th>UPI</th>
            <th>Txn Mode</th>
            <th>Account Type</th>
            <th>Amount</th>
            <th>Status</th>
            <th>UTR</th>
            <th>External Txn</th>
            <th>Remarks</th>
          </tr>
        </thead>
        <tbody>
          {excelData?.map((item, i) => (
            <tr key={item?._id || i}>
              <td>{item?.createdAt || "-"}</td>
              <td>{safe(item?.receiver_name)}</td>
              <td>{safe(item?.bank_name)}</td>
              <td>{safe(item?.ifsc_code)}</td>
              <td>{safe(item?.account_number)}</td>
              <td>{safe(item?.upi_id)}</td>
              <td>{safe(item?.txn_mode)}</td>
              <td>{safe(item?.account_type)}</td>
              <td>{item?.amount ?? 0}</td>
              <td>{safe(item?.status)}</td>
              <td>{safe(item?.utr)}</td>
              <td>{safe(item?.external_txnId)}</td>
              <td>{safe(item?.remarks)}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}

export default PayoutReport;
